<script>
export let href = "#/";
export let target = "_self";
export let redirect = false;

if(href === ""){
    href = null
}

</script>
{#if href !== null}
    {#if !redirect}
        <!-- default layout -->
        <a href="{href}" target="{target}" class="{$$props?.class ?? ""} no-decoration" on:click|preventDefault>
            <slot></slot>
        </a>
        {:else}
            <!-- on normal redirect needed layout (note the on:click event removed to execute a normal redirection) -->
            <a href="{href}" target="{target}" class="{$$props?.class ?? ""} no-decoration">
                <slot></slot>
            </a>
    {/if}
    {:else}
        <!-- Span with href null -->
        <span on:click>
            <slot></slot>
        </span>
{/if}
